// URL for KMB
export const KMB_ROUTE_LIST_LINK = "https://data.etabus.gov.hk/v1/transport/kmb/route/";
export const KMB_ROUTE_LINK = "https://data.etabus.gov.hk/v1/transport/kmb/route/{route}/{direction}/{service_type}";
export const KMB_STOP_LIST_LINK = "https://data.etabus.gov.hk/v1/transport/kmb/stop";
export const KMB_STOP_LINK = "https://data.etabus.gov.hk/v1/transport/kmb/stop/{stop_id}";
export const KMB_ROUTE_STOP_LIST_LINK = "https://data.etabus.gov.hk/v1/transport/kmb/route-stop";
export const KMB_ROUTE_STOP_LINK = "https://data.etabus.gov.hk/v1/transport/kmb/route-stop/{route}/{direction}/{service_type}";
export const KMB_ETA_LINK = "https://data.etabus.gov.hk/v1/transport/kmb/eta/{stop_id}/{route}/{service_type}";
export const KMB_STOP_ETA_LINK = "https://data.etabus.gov.hk/v1/transport/kmb/stop-eta/{stop_id}";
export const KMB_ROUTE_ETA_LINK = "https://data.etabus.gov.hk/v1/transport/kmb/route-eta/{route}/{service_type}"; 

// No. of days to retrieve master data and save to localStorage
export const DAYS_TO_RETRIEVE_KMB_ROUTE_LIST = 7;
export const DAYS_TO_RETRIEVE_KMB_STOP_LIST = 7;
export const DAYS_TO_RETRIEVE_KMB_ROUTE_STOP_LIST = 7;

// Date operation
export const ONE_DAY = 24 * 60 * 60 * 1000;
export const ONE_MIN = 60 * 1000;

export function DayDifference (date1, date2) {
    let parsed_date1 = Date.parse(date1);
    let parsed_date2 = Date.parse(date2);
    let date_diff = parsed_date2 - parsed_date1;
    let day_diff = Math.floor(date_diff / ONE_DAY);
    return day_diff;
}

export function MinDifference (date1, date2) {
    let parsed_date1 = Date.parse(date1);
    let parsed_date2 = Date.parse(date2);
    let date_diff = parsed_date2 - parsed_date1;
    let min_diff = Math.floor(date_diff / ONE_MIN);
    return min_diff;
}

export function etaTimeDifference (date1){
    let timeDifference = MinDifference(new Date(), date1);
    if (timeDifference < 1){
        timeDifference = '-';
    }
    return timeDifference;
}

export function formatTime (date1) {
    let hours = (new Date(date1)).getHours();
    hours = String(hours).padStart(2, '0');
    let minutes = (new Date(date1)).getMinutes();
    minutes = String(minutes).padStart(2, '0');
    
    return hours + ":" + minutes;
}

export function force_clearKMBRouteList (){
    localStorage.removeItem("KMBRouteList_timestamp");
    localStorage.removeItem("KMBRouteList");
}

export function getKMBRouteList (){
    // check if timestamp is more than {param: 7} days before system date
    let local_KMBRouteList_timestamp = localStorage.getItem("KMBRouteList_timestamp");
    let local_KMBRouteList = localStorage.getItem("KMBRouteList");

    if (local_KMBRouteList_timestamp && local_KMBRouteList){
        let days = DAYS_TO_RETRIEVE_KMB_ROUTE_LIST;
        let currDatetime = new Date();
        let day_diff = DayDifference(local_KMBRouteList_timestamp, currDatetime);
        if (day_diff < days){
            console.log("No need to download KMBRouteList");
            return new Promise((resolve) => {
                resolve("Success without download!");
            })
        }
        else {
            console.log("KMBRouteList expired and will be updated.");
        }
    }
    
    return new Promise((resolve)=>{

        axios.get(KMB_ROUTE_LIST_LINK)
        .then((response) => {
            let res_data = response.data;
            let timestamp = res_data.generated_timestamp;
            let data = JSON.stringify(res_data.data);
    
            localStorage.setItem("KMBRouteList_timestamp", timestamp);
            localStorage.setItem("KMBRouteList", data);
            
            resolve("Success!")
        })
    });
}

