export function getKMBRouteETA (route, service_type){
    let link = KMB_ROUTE_ETA_LINK.replace("{route}", route).replace("{service_type}", service_type);

    return new Promise((resolve)=>{

        axios.get(link)
        .then((response) => {
            let res_data = response.data;

            resolve(res_data)
        })
    });
}
